.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#footer {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0; 
  width: 100%;
  height: 2.5rem;            /* Footer height */
}

.carousel-item {
  max-height: 500px;
  overflow: hidden;
  width: 100%;
  }
  .carousel-item img {
  width: 100%;
  }